import React from 'react';
import Navigation from '../components/Navigation';
import {CopyToClipboard} from 'react-copy-to-clipboard'

const Contact = () => {
    return (
        <div className="contact">
        <Navigation/>
        <div className="contactContent">
            <div className="header">

            </div>
            <div className="contactBox">
                <h1>Me contacter</h1>
                <ul>
                    <li>
                        <i className="fas fa-map-marker-alt"></i>
                        <span>Lomé</span>
                    </li>
                    <li>
                        <i className="fas fa-mobile-alt"></i>
                       <CopyToClipboard text="0022890666174">
                        <span onClick={
                            ()=>{alert('Copié')}
                        } className="clickInput">0022890666174</span>
                       </CopyToClipboard>
                    </li>
                    <li>
                        <i className="far fa-envelope"></i>
                       <CopyToClipboard text="nathsenou@gmail.com">
                        <span onClick={
                            ()=>{alert('Email Copié')}
                        } className="clickInput">nathsenou@gmail.com</span>
                       </CopyToClipboard>
                    </li>
                </ul>
            </div>
            <div className="socialNetwork">
            <ul>
                <li>
                    <a target="_blank" href="https://wa.me/22890666174" rel="noopener noreferrer">
                    <i class="fab fa-whatsapp"></i>
                    </a>
                </li>

                                <li>
                    <a target="_blank" href="https://github.com/nathanielyork" rel="noopener noreferrer">
                        <i className="fab fa-github"></i>
                    </a>
                </li>

                <li>
                    <a target="_blank" href="https://www.linkedin.com/in/nathaniel-senou-3b9b4018b/" rel="noopener noreferrer">
                        <i className="fab fa-linkedin"></i>
                    </a>
                </li>

                <li>
                    <a target="_blank" href="https://twitter.com/SenouNathaniel" rel="noopener noreferrer">
                        <i className="fab fa-twitter"></i>
                    </a>
                </li>
            </ul>
        </div>
        
        </div>
        
        </div>
    );
};

export default Contact;