import React, { Component } from 'react'


class Project extends Component {
    state={
        showInfo: false,
    }
    handleInfo=()=>{
        this.setState({
            showInfo:!this.state.showInfo
        })
    }

    render() {
 let {name,languagesIcons,source,info,picture}=this.props.item;
       return (
            <div className="project">
                <div className="icons">
                    {languagesIcons.map((icon)=>{
                        <i className={icon} key={icon}></i>
                    })}
                </div>
                        <h3>{name}</h3>
                        <p>{info}</p>
                        <img src={picture} onClick={this.handleInfo}/>
                        <span className="infos">
                        <i onClick={this.handleInfo} className="fas fa-plus-circle"></i>
                        
                        </span>
                      {
                          this.state.showInfo &&(
                              <div className="showInfos">
                                <div className="infosContent">
                                    <div className="head">
                                    <h2>{name}</h2>
                                    </div>
                                    <p className="text">{info}</p>
                                    <div className="sourceCode">
                                        <a href={source} rel="noopener noreferrer" className="button" target="_blank">Code Source</a>
                                    </div>
                                    <div className="button return" onClick={this.handleInfo}>Retour sur la page</div>
                                    
                              </div>
                              </div>
                          )
                      }
                        </div>
        )
    }
}

export default Project;