import React, { Component } from 'react'
import ProgressBar from './ProgressBar';

class Languages extends Component {
    state={
        languages:[
            {id:'1',value:"PHP",xp:2},
            {id:'2',value:"Java",xp:1},
            {id:'3',value:"SQL",xp:2},
            {id:'4',value:"JAVASCRIPT",xp:2},
        ],
        frameworks:[
            {id:'1',value:"Bootstrap",xp:2},
            {id:'2',value:"Materialise",xp:1},
            {id:'3',value:"Materiel UI",xp:1},
            {id:'4',value:"React",xp:1.5},
            {id:'5',value:"React Native",xp:1},
        ]
     
    }
    render() {
        let {languages,frameworks}=this.state;
        return (
            <div className="languagesFrameworks">
                <ProgressBar
                languages={languages}
                className="lDisplay"
                title="Languages"
                />
                <ProgressBar
                languages={frameworks}
                className="fDisplay"
                title="Bibliotheques"
                />
            </div>
        )
    }
}

export default  Languages;